@import "../less/colors.less";
@import "../less/fonts.less";
@import "../less/variables.less";

.container {
  height: 100%;
  width: 30%;

  background-color: @white;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;

  padding: 2rem;
  box-sizing: border-box;
}

.header {
  color: @primaryColor;
  font-family: @fontRoboto;
  font-size: 50px;
  text-align: center;

  width: 100%;
  border-bottom: 1px solid @secondaryColor;
  padding-bottom: 0.5rem;
}

.description  {
  text-align: center;
  div:first-of-type {
    color: @secondaryColor;
    margin-bottom: 20%;
  }

  div:last-of-type {
    color: @primaryColor;
  }
}

.inputField {
  width: 100%;
  height: 4rem;

  background-color: @secondaryColor;
  color: @white;
  border: none;

  text-align: center;
  text-transform: uppercase;
  font-family: @fontRoboto;
  font-size: 3rem;
  letter-spacing: 10px;
}

.upsell {
  font-family: @fontPermanentMarker;
  font-size: 1.2rem;

  color: @secondaryColor;

  position: absolute;
  left: -5.5rem;
  top: -2rem;

  transform: rotate(-20deg);
}

.arrow {
  position: absolute;
  left: -3rem;
  top: 1rem;

  height: auto;
}
